@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:ital,wght@1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@1,100&family=Roboto+Condensed:ital,wght@1,300&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100&family=Raleway:ital,wght@1,100&family=Roboto+Condensed:ital,wght@1,300&display=swap');
*{
  box-sizing:border-box;
  padding: 0;
  margin: 0;
  /* background-color: #d2dbdd; */
  font-family: "Josefub Sans","Arial Narrow",Arial, Helvetica, sans-serif;
}
.main_div{
  width: 100%;
  height: 100vh;
  background: #6983aa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.center_div{
 width: 25%;
 height: 80vh;
  background-color: #fff;
  box-shadow: 5px 5px 25px -5px rgba(0,0, 0, 0.5);
  border-radius: 15px;
}
h1{
  color: #fff;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
 box-shadow: 5px 5px 15px -5px rgba(0,0, 0, 0.3);
 display: flex;
 justify-content: center;
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-left: 25px;
  
}

button{
  min-height: 40px;
  width: 40px;
 border-color: transparent;
  border-radius: 50%;
  background-color:#8566aa ;
  color: #fff;
 font-size: 40px;
border: none;
outline: none;
margin-left: 10px;
box-shadow: 5px 5px 15px -5px rgba(0,0, 0, 0.3);
}
button:hover{
  background-color: #20bf6b;
}
ol{
  margin-top: 30px;
}
ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color: #8566aa;
  text-transform: capitalize;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
}
.todo_style .fa-times{
  width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  background-color: #8566aa;
  justify-content: center;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0,0, 0, 0.3);
}
.todo_style .fa-times:hover{
  background-color: crimson;
}
.todo_style .fa-times:hover .list_style{
  background-color: crimson;
}
@media(max-width:786px){
  .center_div{
    width: 55%;
  }
}
.todo_style .fa-times{
  margin: 0 15px 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}
button{
  margin-left: 5px;
  min-height: 40px;
  width:40px;
  color: #fff;
  font-size: 30px;
}